.community {
	overflow: hidden;
	.content {
		display: grid;
		grid-template-columns: repeat(2, 340px);
	}

	.board-input {
		padding: 56px 32px;
		border-right: 1px solid rgb(230, 229, 229);
		height: 100vh;

		&-top {
			width: 100%;
			margin-bottom: 64px;

			.title {
				display: flex;
				justify-content: space-between;
				width: 100%;

				h1 {
					@include font(28px);
					font-weight: 300;
					margin-bottom: 24px;
				}
			}
		}

		.input-list {
			.input-item {
				input {
					width: 100%;
					border: 1px solid rgb(230, 229, 229);
					border-radius: 4px;
					padding: 12px 20px;
					margin-bottom: 12px;
				}

				textarea {
					width: 100%;
					min-height: 200px;
					@include font(13px);
					font-weight: 200;
					border: 1px solid rgb(230, 229, 229);
					border-radius: 4px;
					padding: 14px 20px;
				}
			}
		}

		.btns-community-input {
			display: flex;
			justify-content: space-between;
			gap: 10px;

			button {
				cursor: pointer;
				width: 100%;
				background-color: $blue;
				color: #fff;
				height: 56px;
				border-radius: 4px;
				transition: 0.2s;
				border: none;

				&:nth-of-type(1) {
					background-color: #f6f6f6;
					color: #000;
				}

				&:hover {
					text-decoration: underline;
				}
			}
		}
	}

	.board-ouput {
		padding: 32px;

		.board-list {
			.board-item {
				border-bottom: 1px solid rgb(230, 229, 229);
				padding-bottom: 12px;
				position: relative;
				margin-bottom: 20px;

				h2 {
					@include font(14px);
					margin-bottom: 4px;
					padding-right: 48px;
				}

				p {
					@include font(14px, 1.5);
					color: #777;
					font-weight: 300;
					padding-right: 48px;
				}

				.btns-community-ouput {
					position: absolute;
					right: 0;
					top: 0;

					button {
						color: #777;
						cursor: pointer;
						background: none;
						transition: 0.2s;
						width: 28px;
						height: 28px;
						border-radius: 10px;
						border: none;
						@include font(13px, 28px);
						text-align: center;
						margin-right: 5px;
						&:hover {
							&:nth-of-type(1) {
								background-color: #f0eeee;
								color: #000;
							}
							&:nth-of-type(2) {
								background-color: rgba($color: #ff4800, $alpha: 0.1);
								color: #ff4800;
							}
						}
					}
				}
			}

			.input-edit-list {
				padding-right: 56px;
				position: relative;

				input {
					margin-bottom: 4px;
				}

				input,
				textarea {
					width: 100%;
					border: 1px solid rgb(230, 229, 229);
					border-radius: 4px;
					font-weight: 300;
					padding: 4px 8px;
					@include font(13px, 1.5);
				}

				.btns-edit {
					position: absolute;
					right: 0;
					top: 0;

					button {
						@include font(19px);
						color: $blue;
						transition: 0.2;

						&:nth-of-type(1) {
							margin-right: 6px;
							color: #999;
						}
					}
				}
			}
		}
	}
}
